<template>
    <div class="header">
        <div class="pro">
            <div class="plbox">
                <div class="pro_img"><img :src="require(`../img/mo.jpg`)" alt=""></div>
                <div class="pro_txt">
                    <h1>안녕하세요!</h1>
                    <p>Front_end 개발자 구현모 입니다.</p>
                </div>
            </div>
            <div v-html="infor.info" v-show="info1_TF"></div>        
        </div>
        <div class="h_pbox">
            <input class="h_put" type="button" 
            v-for="(m, h_index) in menu" :key="h_index" :value="m" @click="m_link(m.toLowerCase())"> 
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
import iii from './data/info.js'
export default {
    // name : 'he',
    data() {
        return {
            menu : [
                'Profile',
                'Project',
                'Blog',
                // 'About'
            ],
            resize_w : 0,
            resize_h : 0,
            infor : iii,
            info1_TF : true,
        }
    },
    methods : {
        m_link(dex) {
            var router = this.$router;
            router.push({name : dex});

        },
        info_E() {
            var hp_box = document.querySelector('.h_pbox');
            if(this.info_TF == false) {
                hp_box.classList.add('hp_E')
            }else{
                hp_box.classList.remove('hp_E')
            }
        },

        resize_event() { //resize 이벤트
            this.resize_w = window.innerWidth;
            this.resize_h = window.innerHeight;
            if(this.resize_w > 1024) {
                this.info1_TF = true;
            }else if(this.resize_w < 1024) {
                this.info1_TF = false;
            }
        },

        scroll_event() { //scrollTop 이벤트
            var sctop = $(window).scrollTop();
            var hp_box = document.querySelector('.h_pbox');
            var hp_off = $('.h_pbox').offset().top;
            var header_height = $('.header').offset().top + $('.header').height();
            var hp_tf = hp_box.classList.contains('hp_E2');
            if(hp_off <= sctop && this.resize_w < 1024 && hp_tf == false) {
                hp_box.classList.add('hp_E2')
            }else if(header_height >= sctop && this.resize_w < 1024 && hp_tf == true) {
                hp_box.classList.remove('hp_E2')
            }
            
        },
    },
    watch:{
        '$route' (to, from) { // eslint-disable-line no-unused-vars
            var h_put = document.querySelectorAll('.h_put')
            var app_div = document.querySelector('.app_div')
            for (var u = 0; u < h_put.length; u++) {h_put[u].classList.remove("h_E"); app_div.classList.remove('app_back'+u)}
            $('html, body').animate({scrollTop : 0});
            if(to.name == 'profile') {
                h_put[0].classList.add("h_E");
                app_div.classList.add('app_back0')
            }else if(to.name == 'project') {
                h_put[1].classList.add("h_E")
                app_div.classList.add('app_back1')
            }else if(to.name == 'blog') {
                h_put[2].classList.add("h_E")
                app_div.classList.add('app_back2')
            }
        }
    },

    mounted() {
        window.addEventListener('resize', this.resize_event); //resize
        this.resize_w = window.innerWidth;
        window.addEventListener('scroll', this.scroll_event); //scroll
        $('html, body').scrollTop(0);
        if(this.resize_w > 1024) {
            this.info1_TF = true;
        }else if(this.resize_w < 1024) {
            this.info1_TF = false;
        }
    },

    beforeDestory() {
        window.removeEventListener('resize', this.resize_event); //resize
        window.addEventListener('scroll', this.scroll_event); //scroll
    },
}
</script>


