<template>
    <div class="profile">
        <div class="pro_box">
            <div class="info2" v-show="info2_TF">
                <p class="info2_h">INFO</p>
                <div v-html="infor.info"></div>
            </div>
            <div class="inroduce">
                <p class="file">PROFILE</p>
                <p class="pro">안녕하세요. 프론트엔드 개발자 구현모 입니다.<br>
                대학교에서 개발자의 기초적인 역량을 쌓고 6개월 간의 프론트엔드 국비과정을
                배우면서 HTML,CSS,JAVASCRIPT를 활용하여 웹페이지를 제작하고,<br>
                개인프로젝트도 진행하며 개인역량을 쌓아왔습니다.</p>
                <p>가장 좋아하는 언어는 JAVASCRIPT 입니다.<br>
                새로운 기술로 개발하여 사용자 친화적인 웹페이지를 개발하기위해
                항상 발전하는 개발자가 되기위해 노력하고있습니다. 감사합니다
                </p>
            </div>
            <div class="skils">
                <em>SKILLS</em>
                <div class="skil_g">
                    <div class="sk_box" v-for="(s, sdex) in skils" :key="sdex">
                        <div class="sk_img"><img :src="require(`../img/${s.name}.png`)" alt=""></div>
                        <p class="sk_txt">{{s.name}}</p>
                    </div>
                </div>
            </div>

            <div class="storage">
                <em>STORAGE</em>
                <div class="st_g">
                    <div class="st_box" v-for="(st, stdex) in storage" :key="stdex" @click="st_click(stdex)">
                        <div class="st_img"><img :src="require(`../img/${st.name}.png`)" alt=""></div>
                        <p class="st_txt">{{st.name}}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import skil from './data/skils'
import iii from './data/info.js'
    export default {
        data() {
            return {
                skils : skil,
                storage : [
                    {name : 'Github' , link  : 'https://github.com/Yeonhul'}
                ],
                infor : iii,
                info2_TF : true,
                resize_w : 0,
                resize_h : 0
            }
        },
        methods : {
            st_click(index) {
                window.open(this.storage[index].link)
            },
            resize_event() { //resize 이벤트
                this.resize_w = window.innerWidth;
                this.resize_h = window.innerHeight;
                if(this.resize_w > 1024) {
                    this.info2_TF = false;
                }else if(this.resize_w < 1024) {
                    this.info2_TF = true;
                }
            },
        },
        mounted() {
            window.addEventListener('resize', this.resize_event); //resize
            this.resize_w = window.innerWidth;
            if(this.resize_w > 1024) {
                this.info2_TF = false;
            }else if(this.resize_w < 1024) {
                this.info2_TF = true;
            }
        },

    beforeDestory() {
        window.removeEventListener('resize', this.resize_event); //resize
    },
        

    }
</script>


<style>

</style>
