<template >
    <div class="project">
        <div class="ject_box">
            <p class="ject_txt">PROJECT</p>
            <div class="project_list">
                <div class="project_box" v-for="(list, index) in p_list" :key="index">
                    <div class="pl_box">
                        <p>{{ list.name }}</p><em>{{ list.day }}</em>
                        <div class="pli" @click="project_link(index)">
                           <img :src="require(`../img/${list.name}.png`)" alt="" >
                           <span><p>VIEW PROJECT</p></span>
                        </div>
                    </div>
                    <div class="pr_box" >
                        <p class="p_people">{{ list.people }}</p>
                        <h1>개발 언어</h1>
                        <p>{{ list.stack }}</p>
                        <h1>프로젝트 설명</h1>
                        <p v-html="line(index)"></p>
                        <h1>나의 기여도</h1>
                        <p>{{ list.did }}</p>
                        <!-- <input type="button"  value="VIEW POJECT"> -->
                    <p></p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import project_list from './data/project_list'
    export default {
        data() {
            return {
                p_list : project_list
            }
        },
        methods : {
            project_link(index) {
                window.open(this.p_list[index].link)
            },
            line(index) {
                return this.p_list[index].goal.replace(/(?:\r\n|\r|\n)/g,"<br>");
            },
            i_hover() {
                
            }
        },
        computed: {

        }
    }
</script>


